import React, { Component } from 'react';
import { Dropdown } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xdropdown extends Component {
	state = {
		value: new Date(2016, 9, 10, 8, 45),
		value2: [new Date(2016, 9, 10, 8, 40), new Date(2016, 9, 10, 9, 40)]
	}

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	render() {
		const { optionsjs} = this.props.configs

		return (
			<div id="x-time-picker">
				<h3>Dropdown 下拉菜单</h3>
				<div className="mt-20 mb-20">
					<Dropdown type="primary" trigger="click" menu={(
						<Dropdown.Menu>
							<Dropdown.Item>黄金糕</Dropdown.Item>
							<Dropdown.Item>狮子头</Dropdown.Item>
							<Dropdown.Item>螺蛳粉</Dropdown.Item>
							<Dropdown.Item disabled>双皮奶</Dropdown.Item>
							<Dropdown.Item divided>蚵仔煎</Dropdown.Item>
						</Dropdown.Menu>
					)}
					>
						<span className="el-dropdown-link">
							下拉菜单<i className="el-icon-caret-bottom el-icon--right"></i>
						</span>
					</Dropdown>
				</div>
				<Codemirror value={
					`<Dropdown type="primary" trigger="click" menu={(
	<Dropdown.Menu>
		<Dropdown.Item>黄金糕</Dropdown.Item>
		<Dropdown.Item>狮子头</Dropdown.Item>
		<Dropdown.Item>螺蛳粉</Dropdown.Item>
		<Dropdown.Item disabled>双皮奶</Dropdown.Item>
		<Dropdown.Item divided>蚵仔煎</Dropdown.Item>
	</Dropdown.Menu>
)}
>
	<span className="el-dropdown-link">
		下拉菜单<i className="el-icon-caret-bottom el-icon--right"></i>
	</span>
</Dropdown>`
				} options={optionsjs} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

	})(Xdropdown)
